.entries-page {
  display: flex;
  justify-content: center;
  // flex-direction: column;
  // align-items: center;
  // justify-content: flex-start;

  .entries {
    margin-top: 30px;
    max-width: 1200px;
    .header {
      display: flex;
      justify-content: space-between;

      border-bottom: 4px solid $black;
      max-width: 1200px;
      width: 100%;

      .left {
      }

      .right {
        display: flex;
        align-items: center;
      }
    }
    .sortable{
      margin-top: 30px;
      max-width: 1200px;
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-evenly;
    
    .entry-wrapper {
      text-decoration: none;

      display: flex;
      flex-direction: column;
      align-items: center;

      .date {
        margin-bottom: 10px;
      }
    }
  }
  .entries-test {
  }
}
}
