.sideview-functions {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 13px;
  .item {
    height: 18px;
    font-size: 16px;
    margin-left: 10px;
    color: $text-gray;
    &.edit {
      cursor: pointer;
      :hover {
        color: $text;
      }
    }
    &.active {
      color: $text;
    }
    &.delete {
      cursor: pointer;
      :hover {
        color: $warning;
      }
    }
    svg {
      height: 18px;
    }
  }
}
.sideview-item {
  // max-width: 636px;
  padding-top: 70px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .sideview-item-menus {
    width: 100%;
    max-width: 636px;
    display: flex;
    // align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    .tabs {
      display: flex;
      .tab {
        margin-right: 20px;
        a {
          cursor: pointer;
          text-transform: uppercase;
          font-family: Calibri-bold;
          font-size: 21px;
          color: $text-gray;

          &:hover {
            color: $main;
          }
        }
        &.selected {
          a {
            color: $main;
            padding-bottom: 6px;
            position: relative;
            &:after {
              content: "";
              position: absolute;
              bottom: 0;
              left: 0;
              right: 0;
              background: $main;
              height: 4px;
              border-radius: 10px;
            }
          }
        }
      }
    }
    .complete {
      input {
        cursor: pointer;
        text-transform: uppercase;
        border: 2px solid $main;
        background-color: transparent;
        padding: 8px 14px 8px 14px;
        font-size: 14px;
        font-weight: bold;
        border-radius: 10px;
        color: $main;

        &:hover {
          background-color: $main;
          color: white;
        }
      }
    }
  }
  .sideview-content {
    margin-top: 74px;
    max-width: 636px;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    .sideview-content-title {
      font-size: 41px;
      input {
        border: none;
        background: transparent;
        width: 100%;
        color: $text;

        &::placeholder {
          color: $text-gray;
          opacity: 50;
        }
      }
    }
    .sideview-content-body {
      font-size: 21px;
      padding-top: 20px;
      line-height: 32px;
      flex-direction: column;

      textarea {
        width: 100%;
        border: none;
        background: transparent;
      }
    }
  }
}

@media (max-width: 980px) {
  .sideview-item-menus {
    flex-direction: column;

    .complete {
      margin-top: 20px;
    }
  }
}
