.edit-entry {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  .entry-editor {
    align-items: center;
    // margin-left: 200px;
    // margin-right: 200px;

    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;

    .entry-form {
      padding-left: 20px;
      padding-right: 20px;

      .title-input {
        display: flex;

        justify-content: center;
        margin-bottom: 20px;
        padding: 20px;
        // border-radius: 10px;
        // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
        //   0 6px 20px 0 rgba(0, 0, 0, 0.1);

        input {
          width: 100%;
          border: none;
          text-align: center;
        }
      }

      .body-input {
        textarea {
          padding: 30px;
          height: 500px;
          width: 600px;
          border-radius: 10px;
          border: 1px solid #bebebe;
          // box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
          //   0 6px 20px 0 rgba(0, 0, 0, 0.1);
        }
      }
    }
  }
}
