.container {
  display: grid;
  // background-color: #70707038;
  // grid-gap: 1px;
  grid-template-columns: 374px 10px auto;
  // grid-template-rows: auto auto;
  grid-template-areas: "main collapsible side";

  &.collapsed {
    grid-template-columns: 20px 10px auto;

    .content-left {
      display: none;
    }
  }

  .content-left {
    grid-area: main;
  }
  .content-collapse {
    grid-area: collapsible;
  }
  .content-right {
    grid-area: side;
  }

  .content-left {
    background-color: white;

    &.hidden {
      display: none;
    }
  }

  .content-collapse {
    background-color: #fcfdff;
    border-left: 1px solid $lines;
    cursor: w-resize;
  }

  .content-right {
    background-color: #fcfdff;
    padding: 30px;
  }
}

@media (max-width: 980px) {
  .content-right {
    padding: 15px !important;
  }
}
